@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Normalidad";
  font-display: swap;
  src: url("../fonts/NormalidadCompact-Regular.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Normalidad", "Tahoma", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: "Normalidad", "Tahoma", sans-serif;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  body {
    background-color: #fbfaf8;
  }
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 270px 1fr;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}
.wrapper .sidebar {
  grid-column: 1;
}
@media (max-width: 1023px) {
  .wrapper {
    grid-template-columns: 1fr;
    padding: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 10px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 10px;
}
.header__mobile-menu {
  display: none;
  align-items: center;
  gap: 8px;
}
.header__mobile-btn {
  position: relative;
  display: flex;
  width: 40px;
  height: 40px;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
}
.header__mobile-btn--overlook::after {
  content: "";
  position: absolute;
  top: 2px;
  right: 2px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ff0038;
  border: 2px solid #ffffff;
}
@media (max-width: 1023px) {
  .header__mobile-menu {
    display: flex;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px 0;
  }
}

.main {
  display: grid;
  grid-template-columns: 1fr 350px;
  gap: 32px;
  grid-row: 1/4;
  grid-column: 2;
  padding: 16px;
  background-color: #fbfaf8;
  border: 1px solid #efefef;
  border-radius: 56px;
}
.main__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  height: fit-content;
}
@media (max-width: 1023px) {
  .main {
    grid-row: auto;
    grid-column: auto;
    border: none;
    padding: 0;
  }
}
@media (max-width: 1439px) {
  .main {
    grid-template-columns: 1fr;
    gap: 0;
  }
}
@media (max-width: 767px) {
  .main {
    padding-top: 20px;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px 20px 24px 12px;
  gap: 15px;
}
.sidebar__footer {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
@media (max-width: 1023px) {
  .sidebar {
    display: none;
  }
}

.nav {
  padding-left: 12px;
}
.nav__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.nav__link {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  color: #878787;
}
@media (hover: hover) {
  .nav__link:hover:not(.focus-visible) {
    color: #ff5100;
  }
  .nav__link:hover:not(.focus-visible) .icon {
    --icon-bg: #ffe7db;
    --icon-fg: #ff5100;
  }
}
.nav__link.focus-visible:focus {
  color: #ff5100;
}
.nav__link.focus-visible:focus .icon {
  --icon-bg: #ffe7db;
  --icon-fg: #ff5100;
}

.hotline {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 6px;
  padding: 24px;
  border: 1px solid #efefef;
  border-radius: 24px;
  background-color: #fbfaf8;
}
.hotline__title {
  font-weight: 400;
  font-size: 12px;
  line-height: 132%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-align: center;
  color: #878787;
}
.hotline__phone {
  font-weight: 400;
  font-size: 12px;
  line-height: 132%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-align: center;
  color: #535862;
}
.hotline svg {
  position: absolute;
  top: 8px;
  right: 8px;
}

.error-report__btn {
  display: flex;
  align-items: center;
  padding: 0 12px;
  gap: 8px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.error-report span {
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  color: #878787;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 16px;
  background-color: #ffffff;
  backdrop-filter: blur(100px);
  border: 1px solid #efefef;
  border-radius: 40px 40px 48px 48px;
}
.info__inner {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(400px, 100%), 1fr));
  gap: 12px;
}
@media (max-width: 1023px) {
  .info {
    padding: 0;
    border: none;
    background-color: transparent;
  }
}
@media (max-width: 767px) {
  .info {
    gap: 24px;
  }
  .info__inner {
    gap: 20px;
  }
}

.user {
  display: flex;
  justify-content: space-between;
  padding: 0 16px;
}
.user__avatar {
  display: flex;
  align-items: center;
  gap: 12px;
}
.user__avatar img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.user__avatar span {
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  text-align: center;
  color: #535862;
}
.user__city {
  display: flex;
  align-items: center;
  gap: 8px;
  backdrop-filter: blur(100px);
  background: #fbfaf8;
  border-radius: 32px;
  padding: 8px 8px 8px 24px;
}
.user__city span {
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  color: #535862;
}
.user__city b {
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  color: #181d27;
}
@media (max-width: 767px) {
  .user {
    padding: 0;
  }
  .user__city {
    padding: 0;
  }
  .user__city span {
    display: none;
  }
  .user__city b {
    color: #878787;
  }
  .user__city svg {
    width: 20px;
    height: 20px;
  }
}

.help {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 24px;
  background-color: #ff5100;
  border-radius: 26px;
  border: 8px solid #ffe7db;
  color: #ffffff;
}
.help__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 132%;
  color: #ffffff;
}
.help__list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
@media (max-width: 767px) {
  .help {
    padding: 20px;
    gap: 20px;
    border-width: 6px;
    order: 1;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 128%;
  border: none;
  background-color: transparent;
  border-radius: 100px;
  color: inherit;
  cursor: pointer;
  white-space: nowrap;
  transition: 0.3s ease;
}
.btn--help {
  padding: 8px 16px;
  color: #ffffff;
  backdrop-filter: blur(24px);
  background: rgba(255, 255, 255, 0.24);
}
@media (hover: hover) {
  .btn--help:hover:not(.focus-visible) {
    color: #ff5100;
    backdrop-filter: blur(24px);
    background: #ffffff;
  }
}
.btn--help.focus-visible:focus {
  color: #ff5100;
  backdrop-filter: blur(24px);
  background: #ffffff;
}
.btn--tab {
  padding: 8px 16px;
  color: #535862;
}
.btn--tab.active {
  background-color: #efefef;
  pointer-events: none;
}
@media (hover: hover) {
  .btn--tab:hover:not(.focus-visible) {
    background-color: #efefef;
  }
}
.btn--tab.focus-visible:focus {
  background-color: #efefef;
}
.btn--dashboard {
  padding: 12px 24px;
  background-color: #ffe7db;
  color: #ff5100;
}
@media (hover: hover) {
  .btn--dashboard:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: #ff5100;
  }
}
.btn--dashboard.focus-visible:focus {
  color: #ffffff;
  background-color: #ff5100;
}
.btn--dashboard::disabled {
  color: #ffa880;
  background-color: #ffe7db;
}
.btn--dashboard-services {
  padding: 12px 24px;
  background-color: #ffeef6;
  color: #ff0038;
}
@media (hover: hover) {
  .btn--dashboard-services:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: #ff0038;
  }
}
.btn--dashboard-services.focus-visible:focus {
  color: #ffffff;
  background-color: #ff0038;
}

.link {
  font-weight: 400;
  font-size: 14px;
  padding: 0;
  text-decoration: underline dotted;
  text-underline-offset: 4px;
  text-decoration-skip-ink: none;
  text-decoration-thickness: 1.5px;
  text-decoration-color: rgba(135, 135, 135, 0.24);
  color: #878787;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: color 0.3s ease, text-decoration-color 0.3s ease;
}
@media (hover: hover) {
  .link:hover:not(.focus-visible) {
    color: #ff5100;
    text-decoration-color: #ff5100;
  }
}
.link.focus-visible:focus {
  color: #ff5100;
  text-decoration-color: #ff5100;
}
.link--help {
  opacity: 0.8;
  color: #ffffff;
  text-decoration-color: rgba(255, 255, 255, 0.24);
}
@media (hover: hover) {
  .link--help:hover:not(.focus-visible) {
    color: #ffffff;
    text-decoration-color: #ffffff;
  }
}
.link--help.focus-visible:focus {
  color: #ffffff;
  text-decoration-color: #ffffff;
}

.story__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(150px, 100%), 1fr));
  gap: 10px;
  height: 100%;
}
.story__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  gap: 15px;
  padding: 8px;
  border-radius: 32px;
  transition: 0.3s ease;
}
.story__link img {
  width: 134px;
  height: 134px;
  border-radius: 50%;
  object-fit: contain;
}
.story__link p {
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 132%;
  color: #878787;
  border-radius: 8px 8px 24px 24px;
  padding: 16px;
  background-color: #ffffff;
}
.story__link--chablis {
  background-color: #ffe7db;
}
.story__link--tea-rose {
  background-color: #ff85c0;
}
.story__link--dodger-blue {
  background-color: #0986eb;
}
@media (hover: hover) {
  .story__link:hover:not(.focus-visible) p {
    color: #ff5100;
  }
}
.story__link.focus-visible:focus p {
  color: #ff5100;
}
@media (max-width: 767px) {
  .story__list {
    grid-template-columns: repeat(3, 1fr);
  }
  .story__link {
    padding: 4px;
    border-radius: 16px;
  }
  .story__link img {
    width: 80px;
    height: 80px;
  }
  .story__link p {
    font-size: 10px;
    border-radius: 4px 4px 12px 12px;
    padding: 8px 4px 8px 6px;
  }
}

.requests {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 32px 16px;
}
.requests__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.requests__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 132%;
  color: #181d27;
}
.requests__inner {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 0 16px;
}
.requests__tabs-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 4px;
}
.requests__tabs-item {
  flex-shrink: 0;
}
.requests__tabs-wrapper {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.requests__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(300px, 100%), 1fr));
  gap: 12px;
}
.requests__link {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 8px;
  min-height: 170px;
  padding: 16px;
  border: 4px solid #efefef;
  border-radius: 32px;
  height: 100%;
  transition: 0.3s ease;
}
@media (hover: hover) {
  .requests__link:hover:not(.focus-visible) {
    border-color: #ff5100;
  }
}
.requests__link.focus-visible:focus {
  border-color: #ff5100;
}
.requests__link--help {
  background-color: #ffc2df;
}
.requests__link--help h3 {
  font-weight: 400;
  font-size: 20px;
  line-height: 132%;
  color: #ff0038;
}
@media (hover: hover) {
  .requests__link--help:hover:not(.focus-visible) {
    border-color: #ff85c0;
  }
}
.requests__link--help.focus-visible:focus {
  border-color: #ff85c0;
}
.requests__item-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.requests__item-title {
  font-weight: 400;
  font-size: 16px;
  line-height: 250%;
  color: #181d27;
}
.requests__item-buttons {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 1023px) {
  .requests {
    width: calc(100% + 40px);
    margin-left: -20px;
    padding: 32px 0;
    overflow: hidden;
  }
  .requests__header {
    padding: 0 20px;
  }
  .requests__list {
    padding: 0 20px;
  }
  .requests__tabs-list {
    min-width: max-content;
    flex-wrap: nowrap;
    margin-left: 20px;
  }
  .requests__inner {
    padding: 0;
  }
  .requests__tabs-wrapper::-webkit-scrollbar {
    display: none;
  }
  .requests__tabs-wrapper {
    scrollbar-width: none;
  }
}
@media (max-width: 767px) {
  .requests {
    gap: 16px;
    padding: 42px 0;
  }
}

.tag {
  display: inline-flex;
  font-weight: 400;
  font-size: 12px;
  line-height: 132%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-align: center;
  color: inherit;
  border-radius: 26px;
  padding: 6px 12px;
}
.tag--approved {
  color: #02aa03;
  background-color: #dcf3dc;
}
.tag--in-process {
  color: #f6d214;
  background-color: #fef9de;
}

.events {
  padding: 24px 14px 0 0;
}
.events__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.events__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 132%;
  color: #181d27;
}
@media (max-width: 1439px) {
  .events {
    padding: 32px;
  }
}
@media (max-width: 1023px) {
  .events {
    padding: 16px 0;
  }
}
@media (max-width: 767px) {
  .events {
    display: none;
  }
}

.icon {
  --icon-bg: #efefef;
  --icon-fg: #a3a3a3;
}
.icon--active {
  --icon-bg: #ffe7db;
  --icon-fg: #ff5100;
}
.icon--approved {
  --icon-bg: #ddeefc;
  --icon-fg: #0986eb;
}

.footer {
  display: flex;
  flex-direction: column;
  padding: 40px 15px;
  gap: 8px;
}
.footer__copyright {
  font-weight: 400;
  font-size: 12px;
  line-height: 132%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #878787;
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .footer {
    padding: 20px 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 56px 0 20px;
  }
}